.app_header {
    align-items: flex-end;
}

.app_header h1{
    flex-grow: 1;
}

.app_header h2{
    font-size: 1.2rem;
    color: grey;
}
.search-panel {
    margin: 1rem 0;
  }
  
.search-panel .search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
}


.todo-list-item{
    font-size: 1.25rem;
}

.todo-list-item button{
    width: 35px;
    margin: 3px;
}

.todo-list-item-label{
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.todo-list-item.done .todo-list-item-label{
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label{
    font-weight: bold;
    color: steelblue;
}
.todo-list .list-group-item{
    padding: .25rem .75rem;
}
.item-add-form{
    margin-top: 10px;
}

.item-add-form input{
    margin-right: 3px;
}

.item-add-form button{
    width: 110px;
}
.todo_app {
    margin: 2rem auto 0 auto;
    max-width: 500px;
  }
